export default {
	styles: String,
	disableScroll: {
		type: Boolean,
		default: true
	},
	type: {
		type: String,
		default: '2d'
	},
	// 
	penColor: {
		type: String,
		default: 'black'
	},
	penSize: {
		type: Number,
		default: 2
	},
	// 
	backgroundColor: String,
	backgroundImage: String,
	// 
	openSmooth: Boolean,
	// 
	minLineWidth: {
		type: Number,
		default: 2
	},
	// 
	maxLineWidth: {
		type: Number,
		default: 6
	},
	// (px/ms)1.0-10.0
	minSpeed: {
		type: Number,
		default: 1.5
	},
	// ()1-100maxWidthDiffRate
	maxWidthDiffRate: {
		type: Number,
		default: 20
	},
	// 0
	maxHistoryLength: {
		type: Number,
		default: 20
	},
	beforeDelay: {
		type: Number,
		default: 0
	},
	landscape: {
		type: Boolean
	},
	boundingBox: {
		type: Boolean
	},
	disabled: {
		type: Boolean
	},
	preferToDataURL: Boolean
}