import url from "./url.js";
let {
	WdefBaseUrl
} = url;

// 
const timeout = 60000;

// token
export default (params) => {
	let url = params.url;
	let method = params.method || "get";
	let data = params.data || {};
	let responseType = params.responseType || "text";
	let header = {
		token: uni.getStorageSync("token") || "",
		"Content-Type": params.header || "application/json;charset=UTF-8",
		Authorization: `Basic  + ${uni.getStorageSync("token")}`,
	};
	return new Promise((resolve, reject) => {
		uni.request({
			url: WdefBaseUrl + url,
			method: method,
			header: header,
			data: data,
			timeout,
			responseType,
			success: (res) => {
				if (res.data && res.data.code == "0") {
					resolve(res.data);
				} else {
					console.log(res.data,'18574812093');
					if (res.data.code == "401" || res.data.msg == "token" ||  res.data.msg == "token") {
						uni.showToast({
							title: "",
							icon: "none",
						});
						uni.removeStorageSync("token")
						setTimeout(() => {
							uni.reLaunch({
								url: "/pages/login/login"
							})
						}, 1000);
					} else {
						uni.showToast({
							title: res.data.msg,
							icon: "none",
						});
						resolve(res.data);
					}
				}
			},
			fail(err) {
				if (err && err.errMsg === "request:fail timeout") {
					// 
					uni.showToast({
						title: ",!",
						icon: "none",
					});
					// 
				} else {
					// 
					uni.showToast({
						title: res.data.msg,
						icon: "none",
					});
				}
				reject(err); // 
			},
			complete() {
				// 
			},
		});
	}).catch(() => {});
};